  font-weight: 700;
  font-size: 2.2rem;
}

h4 {
  font-weight: 500;
  font-size: 1.44rem;
}

h5 {
  font-weight: 400;
  font-size: 2.2rem;
  line-height: 3.2rem;
  font-family: "Raleway", sans-serif;
}

p {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 28px;
  font-family: "Raleway", sans-serif;
  margin: 0;
}

h6 {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.8rem;
  font-family: "Raleway", sans-serif;
  margin: 0;
}

ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

span {
  margin: 0;
  padding: 0;
  transition: all ease 0.39s;
}

a {
  color: #f0f8ff;
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

@media only screen and (max-width: 1023px) {
  h1 {
    font-size: 4rem;
    font-weight: 700;
    line-height: 1;
  }

  h2 {
    font-weight: 700;
    font-size: 2.6rem;
    line-height: 3.2rem;
  }

  h3 {
    font-weight: 700;
    font-size: 1.7rem;
  }

  h4 {
    font-weight: 500;
    font-size: 1.2rem;
  }

  h5 {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 2.8rem;
  }

  p {
    font-weight: 400;
    font-size: 1.44rem;
    line-height: 18px;
    font-family: "Raleway", sans-serif;
  }

  h6 {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.4rem;
    font-family: "Raleway", sans-serif;
    margin: 0;
  }
}

/* Animations */

.animBottomUp {
  opacity: 0;
  margin-top: 0%;
  animation: bottomUp 0.3s ease forwards;
}

@keyframes bottomUp {
  from {
    opacity: 0;
    margin-top: 60px;
  }
  to {
    opacity: 1;
    margin-top: 0px;
  }
}

.animOpacity {
  opacity: 0;
  animation: animOpacity 0.3s ease forwards;
}

@keyframes animOpacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Animation Delays*/

.delay1 {
  animation-delay: 0.1s;
}

.delay2 {
  animation-delay: 0.2s;
}

.delay3 {
  animation-delay: 0.3s;
}

.delay4 {
  animation-delay: 0.4s;
}

.delay5 {
  animation-delay: 0.5s;
}

.delay6 {
  animation-delay: 0.6s;
}

.noOverflow {
  overflow: hidden;
}

